<template>
  <div class="amount-container">
    <div class="amount-inner">
      <!-- 数据显示 -->
      <ul class="amount-datas" :class="{ hasLine }">
        <li>
          <label>当前额度</label>
          <span>{{ data.totalAmount | fmtMoney }}</span>
        </li>
        <li>
          <label>已用额度</label>
          <span>{{ data.usedAmount | fmtMoney }}</span>
        </li>
        <li>
          <label>可用额度</label>
          <span>{{ data.availableAmount | fmtMoney }}</span>
        </li>
        <li v-if="hasLine">
          <label>预算额度警戒线</label>
          <span>{{ data.budgetAmountLine | fmtFloat2Percent }}</span>
        </li>
        <li>
          <label>预算额度</label>
          <span>{{ data.budgetAmount | fmtMoney }}</span>
        </li>
        <li>
          <label>备用额度</label>
          <span>{{ data.imprestAmount | fmtMoney }}</span>
        </li>
        <li>
          <label>已发行积分</label>
          <span>{{ data.issued | fmtMoney }}</span>
        </li>
        <li v-if="hasLine">
          <label>备用额度警戒线</label>
          <span>{{ data.imprestAmountLine | fmtFloat2Percent }}</span>
        </li>
      </ul>

      <!-- 直观图 -->
      <div class="amount-chart">

        <div class="amount-budget" :style="{ width: budgetWidth }" :title="budgetTips">
          <label>预算额度</label>
          <div class="budget-line" :style="{ left: budgetLinePos }" v-if="hasLine && data.budgetAmountLine"></div>
        </div>

        <div class="amount-imprest" :style="{ width: imprestWidth }" :title="imprestTips">
          <label>备用额度</label>
          <div class="imprest-line" :style="{ left: imprestLinePos }" v-if="hasLine && data.imprestAmountLine"></div>
        </div>

        <div class="budget-used" :style="{ width: usedWidth }" v-show="usedWidth !== '-'">
          <span>已用额度：<strong>{{ usedWidth }}</strong></span>
          <div class="budget-issued" :style="{ width: issuedWidth }" v-show="issuedWidth !== '-'">
            <span>已发行：<strong>{{ issuedWidth }}</strong></span>
          </div>
        </div>

      </div>
    </div>
  </div>
</template>

<script>
import { fmtMoney, fmtFloat2Percent } from '@/filters'

export default {
  name: 'amount-acct',
  props: {
    data: {
      type: Object,
      required: true
    },
    hasLine: {
      type: Boolean,
      default: true
    }
  },
  computed: {
    budgetWidth () {
      const w = fmtFloat2Percent(this.data.budgetAmount / (this.data.budgetAmount + this.data.imprestAmount))
      return w === '-' ? '50%' : w
    },
    imprestWidth () {
      return 100 - parseFloat(this.budgetWidth) + '%'
    },
    budgetLinePos () {
      return fmtFloat2Percent(this.data.budgetAmountLine)
    },
    imprestLinePos () {
      return fmtFloat2Percent(this.data.imprestAmountLine)
    },
    usedWidth () {
      return fmtFloat2Percent(this.data.usedAmount / (this.data.budgetAmount + this.data.imprestAmount))
    },
    issuedWidth () {
      return fmtFloat2Percent(this.data.issued / this.data.usedAmount)
    },
    budgetTips () {
      return '预算费用（元）：' + fmtMoney(this.data.budget)
    },
    imprestTips () {
      return '备用金（元）：' + fmtMoney(this.data.imprest)
    }
  }
}
</script>

