        background-color: #ed4014;
      }

      .budget-used,
      .budget-issued {
        position: absolute;
        bottom: 0;
        left: 0;
        top: 0;
        pointer-events: none;

        &>span {
          position: absolute;
          right: 6px;
          transform: translateX(100%);
          min-width: 150px;
          font-size: 12px;

          strong { font-size: 16px; }

          &::before {
            content: '';
            position: absolute;
            left: 2px;
